.contact-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;

  }
  
  .contact-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
  }
  
  .contact-subtitle {
	width: 100% !important;
  }
  
  .contact-container {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	margin-top: 120px;
  }
  
  .socials-container {
	display: flex;
	flex-direction: column;
	margin-top: 80px;
  }
  
  .contact-socials {
	display: flex;
	justify-content: flex-start;
	align-items: center;
  }
  

  .contact-form
  {
	margin-top: 60px;
	width: calc(100% - 16px);  }
  .contact-div
  {
	display: flex;
	flex-direction: column;
	grid-gap: 30px;
  }
  .contact-form input
  {
	height: 40px;
	padding: 4px 16px;
  }
  .contact-form input,
  .contact-form textarea {
	max-width: 100%;
	width: 50%;
	
	border-radius: 20px;
	border: none;
	background: white;
	box-shadow: 12px 16px 32px -16px rgba(0, 0, 0, 0.1) !important;
	outline-offset: 2px;
	text-rendering: antialiased;
  }
  
.contact-form textarea
{
	min-height: 200px;
	max-height: 400px;
	padding: 16px 16px;
}

  .contact-form button {
	padding: 8px 16px;
	background-color: var(--primary-color);
	color: #fff;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	border-radius: 24px;
	margin-top: 30px;
  }
  
  @media (max-width: 600px)
  {
	.contact-form input,
	.contact-form textarea {
	  max-width: 100%;
	  width: 100%;
	  
	  border-radius: 20px;
	  border: none;
	  background: white;
	  box-shadow: 12px 16px 32px -16px rgba(0, 0, 0, 0.1) !important;
  
	}
	.contact-form
	{
	  margin-top: 60px;
	  width: calc(90%);
	}
  } 