.pricing-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 70%;
    margin: 0 auto;
    padding-top: 120px;
  }
  
  .tabs {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .tab {
    padding: 10px 20px;
    border: 1px solid #ccc;
    border-radius: 20px;
    cursor: pointer;
  }
  
  .tab.active {
    background-color: #ccc;
  }
  
  .plans {
    display: grid;
    flex-direction: row;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
  }
  
  .plan {
    background-color: #f5f5f5;
    border-radius: 24px;
    padding: 20px;
    text-align: center;
    width: calc(100% - 40px);
    margin: 0 auto;
  }
  
  .price {
    font-size: 24px;
    font-weight: bold;
    margin: 20px 0;
  }
  
  .monthly-payment {
    font-size: 14px;
    font-style: italic;
    margin-bottom: 10px;
    background: var(--primary-color);
    padding: 4px 6px;
    color: #fff;
    border-radius: 12px;
    width: fit-content;
    margin: 0 auto;
  }
  
  .features {
    list-style-type: disc;
    padding: 16px 20px;
    height: 100px;
    width: 80%;
    margin: 20px auto;
    overflow-y: scroll;
    text-align: left;
    border-radius: 24px;
  }
  
  .features li {
    margin-bottom: 5px;
  }
  
  button {
    padding: 8px 16px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  @media (max-width: 1200px) {
    .plan {
      background-color: #f5f5f5;
      border-radius: 24px;
      padding: 20px;
      text-align: center;
      width: calc(90% );
      margin: 0 auto;
    }
  
    .plans {
      display: flex;
      flex-direction: column;
      grid-template-columns: 1fr 1fr;
      grid-gap: 20px;
      padding: 0;
      width: 100%;
    }
  
    .pricing-container {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;
      margin: 0 auto;
      padding-top: 120px;
    }
  
    .tabs {
      display: flex;
      gap: 10px;
      flex-direction: row;
      margin-bottom: 20px !important;
      flex-wrap: wrap;
      justify-content: left;
      width: 90%;
      margin: 0;
    }
  
    .tab {
      padding: 8px 16px;
      border: 1px solid #ccc;
      border-radius: 20px;
      cursor: pointer;
      height: 40px;
      width: fit-content;
    }
  
    .features {
      overflow: visible;
      height: auto;
    }
  }
  